.box {
    background-color: #fff;
    border: none;
    box-shadow: 0px 0px 10px #888;
    border-radius: 10%;
    width: 5rem;
    height: 5rem;
    font-size: 5em;
    text-align: center;
    font-weight: bold;
    line-height: 5rem;
    margin: 0.5rem;
  }
  .box:hover {
    box-shadow: 0px 0px 15px #888;
  }
  .x {
    color: rgb(197, 1, 1);
  }
  
  .o {
    color: rgb(68, 68, 68);
  }